.card {
    width: 14.6vw;
    height: 21.88vw;

    border-radius: 1.125rem;
    box-shadow: 0 7px 10px 0 rgba(0, 0, 0, 0.45);
    position: relative;

}

.cardImg {
    width: 100%;
    height: 100%;
    cursor: pointer;
    border-radius: 1.125rem;
    position: relative;
}

.bottom {
    position: absolute;
    display: flex;
    align-items: center;
    bottom: 1%;
    margin-left: 1rem;
    gap: 0.3rem;
}

.top {
    position: absolute;
    display: flex;
    align-items: center;
    top: 0;
    right: 0;
    margin-top: 0.88rem;
    margin-right: 1.12rem;
    gap: 0.25rem;
}

.matchPerc {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 3.22vw;
    height: 1.97vw;
    gap: 0.125rem;
    border-radius: 0.75rem;
    background: rgba(233, 233, 233, 0.05);
    backdrop-filter: blur(24px);
    margin-top: 3px;

}
.matchPerc img{
    width: 1.15vw;
}

.name {
    color: #FFF;
    text-align: center;
    font-family: Montserrat, sans-serif;
    font-size: 1.88vw !important;
    font-style: normal;
    font-weight: 600;
    line-height: 145%;
    text-transform: capitalize;
    margin: 0;
}

.percNumber {
    color: #D9D9D9;
    text-align: center;
    font-family: Onest, sans-serif;
    font-size: 0.9375vw !important;
    font-style: normal;
    font-weight: 500;
    line-height: 145%;
    margin: 0;
}

.gameSection {
    display: flex;
    height: 1.66vw;
    justify-content: center;
    align-items: center;
    gap: 0.3rem;
    border-radius: 1.125rem;
    background: rgba(233, 233, 233, 0.05);
    backdrop-filter: blur(24px);
    padding: 0 0.5rem 0 0.5rem;
}

.gameSection img {
    width: 1.145vw;
    height: 1.145vw;
}

.sound {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1.66vw;
    height: 1.66vw;
    border-radius: 1.125rem;
    background: rgba(233, 233, 233, 0.05);
    backdrop-filter: blur(24px);

}

.sound img {
    width: 1.04vw;
    height: 1.04vw;
    flex-shrink: 0;

}
.verified{
    width: 1.5625vw;
}
@media screen and (max-width: 1000px) {
    .card {
        width: 11.38rem;
        height: 17.13rem;
        border-radius: 1.125rem;
    }
    .sound {
        width: 1.375rem;
        height: 1.375rem;
    }
    .sound img {
        width: 0.70713rem;
        height: 0.70713rem;
    }
    .gameSection {
        height: 1.38rem;
        gap: 0.13rem;
        border-radius: 1.125rem;
        padding: 0 0.5rem 0 0.5rem;
    }

    .gameSection img {
        width: 1.25rem;
        height: 1.25rem;
    }
    .matchPerc {
        width: 2.9375rem;
        height: 1.6875rem;
        gap: 0.125rem;
        border-radius: 0.625rem;

    }
    .matchPerc img{
        width: 0.6875rem;
        height: 0.6875rem;
    }

    .name {
        font-size: 1.25rem !important;
        font-family: Montserrat, sans-serif !important;
        font-weight: 600;

    }

    .percNumber {
        font-size: 1rem !important;
    }
    .verified{
        width: 1.3125rem;
        height: 1.3125rem;
    }
}
